
@charset "UTF-8";
.ci-container__router-view {
  width: 100%;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.pop-out-enter-active,
.pop-out-leave-active,
.pop-in-enter-active,
.pop-in-leave-active {
  will-change: transform;
  height: 100%;
  position: absolute;
  left: 0;
}
.pop-out-enter-active {
  -webkit-animation-name: popInLeft;
          animation-name: popInLeft;
}
.pop-out-leave-active {
  -webkit-animation-name: popOutRight;
          animation-name: popOutRight;
}
.pop-in-enter-active {
  -webkit-perspective: 1000;
          perspective: 1000;
  -webkit-animation-name: popInRight;
          animation-name: popInRight;
}
.pop-in-leave-active {
  -webkit-animation-name: popOutLeft;
          animation-name: popOutLeft;
}
.header-fade-in-right-enter-active {
  -webkit-animation: fadeinR .5s;
          animation: fadeinR .5s;
}
.header-fade-in-left-enter-active {
  -webkit-animation: fadeinL .5s;
          animation: fadeinL .5s;
}
@-webkit-keyframes popInLeft {
from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
}
to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@keyframes popInLeft {
from {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
}
to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@-webkit-keyframes popOutLeft {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
}
}
@keyframes popOutLeft {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
}
}
@-webkit-keyframes popInRight {
from {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@keyframes popInRight {
from {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@-webkit-keyframes popOutRight {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
}
@keyframes popOutRight {
from {
    opacity: 1;
}
to {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
}
@-webkit-keyframes fadeinR {
0% {
    opacity: 0;
    -webkit-transform: translateX(150px);
            transform: translateX(150px);
}
100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@keyframes fadeinR {
0% {
    opacity: 0;
    -webkit-transform: translateX(150px);
            transform: translateX(150px);
}
100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@-webkit-keyframes fadeinL {
0% {
    opacity: 0;
    -webkit-transform: translateX(-150px);
            transform: translateX(-150px);
}
100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@keyframes fadeinL {
0% {
    opacity: 0;
    -webkit-transform: translateX(-150px);
            transform: translateX(-150px);
}
100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}

/** 处理安卓下适配问题 */
* {
  max-height: 1000000px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
}
body {
  font-size: .16rem;
}
body .ci-container {
    min-height: 100vh;
    overflow: hidden;
}
.ci-container__lanuage-tab {
  margin-top: .2rem;
}



